.page-header {

    figure {
        width: 23rem;
        height: 23rem;
        margin-bottom: 1rem;
    }

    & > * {
        text-align: center;
        width: min(82rem, 100%);
        margin: 0 auto;
    }

    h1 {
        font-size:clamp(4rem, 3.772rem + 0.506vw, 4.4rem);
        font-weight: 700;
        text-transform: uppercase;
    }

    p {
        margin-top: 1.4rem;

        font-size: clamp(2rem, 1.886rem + 0.253vw, 2.2rem);
        font-weight: 400;
    }
}